import React from 'react';
import jsPDF from 'jspdf';
import './pendaftaran.css';
import   { adddata} from '../../Pages/Admin/firebase/firebase.utils';
import Field from './field';

const Register = () => {

    const dataaawal= {
        namalengkap : '',
        tempatdantanggallahir : '',
        nik : '',
        nisn:'',
        jeniskelamin:'',
        agama:'',
        alamat:'',
        kelurahan:'',
        kecamatan:'',
        kota:'',
        provinsi:'',
        kodepos:'',
        noijazah:'',
        noskhu:'',
        tahuntamat:'',
        rencanaprogram:'',
        email:'',
        nohppribadi:'',
        namaayah:'',
        tahunlahirayah:'',
        pekerjaanayah:'',
        pendidikanterakhirayah:'',
        nohpayah:'',
        namaibu:'',
        tahunlahiribu:'',
        pekerjaanibu:'',
        pendidikanterakhiribu:'',
        nohpibu:'',
        namawali:'',
        tahunlahirwali:'',
        pekerjaanwali:'',
        pendidikanterakhirwali:'',
        nohpwali:'',

    }

    const [datadaftar,setdatadaftar] = React.useState(dataaawal)



  
    const handleupload =async (e)=>{
        // const storageRef = firebase.storage().ref(`images/${datadaftar.cover.name}`)
        // await storageRef.put(cover)
        // const data =  await storageRef.getDownloadURL()
        const tgldaftar = new Date();
        if(datadaftar.namalengkap &&
             datadaftar.tempatdantanggallahir &&
              datadaftar.nik &&
               datadaftar.jeniskelamin&&
               datadaftar.agama&&
               datadaftar.alamat&&
               datadaftar.kelurahan&&
               datadaftar.kecamatan&&
               datadaftar.provinsi&&
               datadaftar.rencanaprogram&&
               datadaftar.nohppribadi&&
               datadaftar.namaayah&&
               datadaftar.tahunlahirayah&&
               datadaftar.pekerjaanayah&&
               datadaftar.pendidikanterakhirayah&&
               datadaftar.nohpayah&&
               datadaftar.namaibu&&
               datadaftar.tahunlahiribu&&
               datadaftar.pekerjaanibu&&
               datadaftar.pendidikanterakhiribu&&
               datadaftar.nohpibu&&
               datadaftar.namawali&&
               datadaftar.tahunlahirwali&&
               datadaftar.pekerjaanwali&&
               datadaftar.pendidikanterakhirwali &&
                datadaftar.nohpwali !== ""){
            adddata('pendaftar',{...datadaftar,tgldaftar : tgldaftar})
            alert('data sudah ditambah')
            pdfgenerator()
            
            
         }
         else{
            
            alert("data belum lengkap")
         }
        }

       const pdfgenerator = ()=>{

            var doc = new jsPDF('p','pt');
    
            doc.text(20,70,`Nama Lengkap            `)   
            doc.text(150,70, `   : ${datadaftar.namalengkap}`)
            doc.text(20,90,`Tempat/tgl lahir        `)   
            doc.text(150,90, `   :${datadaftar.tempatdantanggallahir}`)
            doc.text(20,110,`NIK                    `)   
            doc.text(150,110, `    : ${datadaftar.nik}`)
            doc.text(20,130,`NISN                   `)   
            doc.text(150,130, `    : ${datadaftar.nisn}`)
            doc.text(20,150,`Jenis Kelamin          `)   
            doc.text(150,150, `   : ${datadaftar.jeniskelamin}`)
            doc.text(20,170,`Agama                  `)   
            doc.text(150,170, `   : ${datadaftar.agama}`)
            doc.text(20,190,`Alamat                 `)   
            doc.text(150,190, `   : ${datadaftar.alamat}`)
            doc.text(20,210,`Kelurahan              `)   
            doc.text(150,210, `   : ${datadaftar.kelurahan}`)
            doc.text(20,230,`Kecamatan              `)   
            doc.text(150,230, `   : ${datadaftar.kecamatan}`)
            doc.text(20,250,`Kota                   `)   
            doc.text(150,250, `   : ${datadaftar.kota}`)
            doc.text(20,270,`Provinsi               `)   
            doc.text(150,270, `   : ${datadaftar.provinsi}`)
            doc.text(20,290,`Kode Pos               `)   
            doc.text(150,290, `   : ${datadaftar.kodepos}`)
            doc.text(20,310,`No Ijazah              `)   
            doc.text(150,310, `   : ${datadaftar.noijazah}`)
            doc.text(20,330,`No SKHU                `)   
            doc.text(150,330, `   : ${datadaftar.noskhu}`)
            doc.text(20,350,`Tahun Tamat            `)   
            doc.text(150,350, `   : ${datadaftar.tahuntamat}`)
            doc.text(20,370,`Rencana Program        `)   
            doc.text(150,370, `   : ${datadaftar.rencanaprogram}`)
            doc.text(20,390,`Email                  `)   
            doc.text(150,390, `   : ${datadaftar.email}`)
            doc.text(20,410,`No HP                  `)   
            doc.text(150,410, `   : ${datadaftar.nohppribadi}`)
            doc.text(20,430,`Nama Ayah              `)   
            doc.text(150,430, `   : ${datadaftar.namaayah}`)
            doc.text(20,450,`Tahun Lahir            `)   
            doc.text(150,450, `   : ${datadaftar.tahunlahirayah}`)
            doc.text(20,470,`Pekerjaan    `)   
            doc.text(150,470, `   : ${datadaftar.pekerjaanayah}`)
            doc.text(20,490,`Pendidikan Terakhir    `)   
            doc.text(150,490, `   : ${datadaftar.pendidikanterakhirayah}`)
            doc.text(20,510,` No Hp                 `)   
            doc.text(150,510, `   : ${datadaftar.nohpayah}`)
            doc.text(20,530,`Nama Ibu               `)   
            doc.text(150,530, `   : ${datadaftar.namaibu}`)
            doc.text(20,550,`Tahun Lahir            `)   
            doc.text(150,550, `   : ${datadaftar.tahunlahiribu}`)
            doc.text(20,570,`Pekerjaan    `)   
            doc.text(150,570, `   : ${datadaftar.pekerjaanibu}`)
            doc.text(20,590,`Pendidikan Terakhir    `)   
            doc.text(150,590, `   : ${datadaftar.pendidikanterakhiribu}`)
            doc.text(20,610,` No Hp                 `)   
            doc.text(150,610, `   : ${datadaftar.nohpibu}`)
            doc.text(20,630,`Nama Wali              `)   
            doc.text(150,630, `   : ${datadaftar.namawali}`)
            doc.text(20,650,`Tahun Lahir            `)   
            doc.text(150,650, `   : ${datadaftar.tahunlahirwali}`)
            doc.text(20,670,`Pekerjaan    `)   
            doc.text(150,670, `   : ${datadaftar.pekerjaanwali}`)
            doc.text(20,690,`Pendidikan Terakhir    `)   
            doc.text(150,690, `   : ${datadaftar.pendidikanterakhirwali}`)
            doc.text(20,710,` No Hp                 `)   
            doc.text(150,710, `   : ${datadaftar.nohpwali}`)




            doc.save("pendaftar.pdf")
        }

     const   handleChange = event => {
            const { value, name } = event.target;
        
            setdatadaftar({...datadaftar,[name]:value})
          };


    return (<div className="black bodypendaftaran">
        <article className=" pv5 br3 ba bg-white b--black-10  w-100 pb3 w-50-m w-25-l mw6 shadow-5 center">
           
            <main className="pa4 black-80">

                <fieldset id="sign_up" className="ba b--transparent ph0 mh0">
                    <legend className="f2 blue fw6 ph0 mh0">Pendaftaran Siswa Baru</legend>
                    <legend className="f4 fw6 ph0 mh0">Data Pribadi</legend>
                    <Field labelfield='Nama Lengkap' handlechange={handleChange} namefield='namalengkap'/>
                    <Field labelfield='Tempat/tgl lahir' handlechange={handleChange} namefield='tempatdantanggallahir'/>
                    <Field labelfield='NIK' handlechange={handleChange} namefield='nik'/>
                    <Field labelfield='NISN' handlechange={handleChange} namefield='nisn'/>                    
                    <div className="mt3">
                        <label className="db fw6 lh-copy f6 " >Jenis Kelamin</label>
                        <select id="category" name="category" onChange={(e)=>setdatadaftar({...datadaftar,jeniskelamin : e.target.value})}>
                        <option value="">...</option>
                            <option value="Laki-laki">Laki-laki</option>
                            <option value="Perempuan">Perempuan</option>
                        </select>
                    </div>
                    <Field labelfield='Agama' handlechange={handleChange} namefield='agama'/>
                    <Field labelfield='Alamat' handlechange={handleChange} namefield='alamat'/>  
                    <Field labelfield='Kelurahan' handlechange={handleChange} namefield='kelurahan'/>  
                    <Field labelfield='Kecamatan' handlechange={handleChange} namefield='kecamatan'/>  
                    <Field labelfield='Kota' handlechange={handleChange} namefield='kota'/>  
                    <Field labelfield='Provinsi' handlechange={handleChange} namefield='provinsi'/>  
                    <Field labelfield='Kode Pos' handlechange={handleChange} namefield='kodepos'/>  
                   
                    <div className="mt3">
                        <label className="db fw6 lh-copy f6 " >Rencana Program</label>
                        <select id="category" name="category" onChange={(e)=>setdatadaftar({...datadaftar,rencanaprogram: e.target.value})}>
                        <option value="">...</option>
                            <option value="A">A</option>
                            <option value="B">B</option>
                            <option value="C">C</option>
                        </select>
                    </div>

                  {
                      datadaftar.rencanaprogram === 'B' || datadaftar.rencanaprogram==='C'   ? <div>
                          <Field labelfield='No Ijazah' handlechange={handleChange} namefield='noijazah'/>  
                          <Field labelfield='No SKHU' handlechange={handleChange} namefield='noskhu'/>  
                          <Field labelfield='Tahun Tamat' handlechange={handleChange} namefield='tahuntamat'/>  </div>
                      : null
                  }

                     
                   <Field labelfield='Email' handlechange={handleChange} namefield='email'/>  
                   <Field labelfield='No Hp' handlechange={handleChange} namefield='nohppribadi'/>  

                    <legend className="f3 fw6 ph0 mh0">Data Orang tua</legend>
                    <Field labelfield='Nama Ayah' handlechange={handleChange} namefield='namaayah'/>  
                    <Field labelfield='Tahun Lahir' handlechange={handleChange} namefield='tahunlahirayah'/>  
                    <Field labelfield='Pekerjaan' handlechange={handleChange} namefield='pekerjaanayah'/>  
                    <Field labelfield='Pendidikan Terakhir' handlechange={handleChange} namefield='pendidikanterakhirayah'/>  
                    <Field labelfield='No. HP' handlechange={handleChange} namefield='nohpayah'/>  
                    <Field labelfield='Nama Ibu' handlechange={handleChange} namefield='namaibu'/>  
                    <Field labelfield='Tahun Lahir' handlechange={handleChange} namefield='tahunlahiribu'/>  
                    <Field labelfield='Pekerjaan' handlechange={handleChange} namefield='pekerjaanibu'/>  
                    <Field labelfield='Pendidikan Terakhir' handlechange={handleChange} namefield='pendidikanterakhiribu'/>  
                    <Field labelfield='No. Hp' handlechange={handleChange} namefield='nohpibu'/>  
                    <Field labelfield='Nama Wali' handlechange={handleChange} namefield='namawali'/>  
                    <Field labelfield='Tahun Lahir' handlechange={handleChange} namefield='tahunlahirwali'/>  
                    <Field labelfield='Pekerjaan Wali' handlechange={handleChange} namefield='pekerjaanwali'/> 
                    <Field labelfield='Pendidikan Terakhir' handlechange={handleChange} namefield='pendidikanterakhirwali'/>  
                    <Field labelfield='No. Hp' handlechange={handleChange} namefield='nohpwali'/>  
                </fieldset>
                <div className="">
                    <input  onClick={handleupload} className="btn btn-primary" type="submit" value="Daftar" />
                </div>
            </main>
        </article>
        </div>)
}

export default Register;